* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --bg-color: #ffffff;
  --fg-color: #121111;
  --header-bg: #f5f5f5;
  --accent-color: #ef9f45;
  --modal-bg: #ffffff;
  --modal-overlay: rgba(0, 0, 0, 0.5);
  --input-bg: #f5f5f5;
  --input-border: #ddd;
  --header-height: 52px;
  --footer-height: 32px;
}

:root.dark {
  --bg-color: #121111;
  --fg-color: #d2d2d2;
  --header-bg: #040404;
  --modal-bg: #1a1a1a;
  --input-bg: #2a2a2a;
  --input-border: #333;
}

html {
  font-size: 22px;
  background: var(--bg-color);
  color: var(--fg-color);
}

body {
  min-height: 100vh;
  font-family: 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

header {
  width: 100%;
  height: var(--header-height);
  padding: 12px;
  background-color: var(--header-bg);
  color: var(--accent-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}

main {
  flex: 1;
  width: 100%;
  position: fixed;
  top: var(--header-height);
  bottom: var(--footer-height);
  left: 0;
  overflow: hidden;
  display: flex;
}

textarea {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  color: var(--fg-color);
  resize: none;
  padding: 12px;
}

footer {
  width: 100%;
  height: var(--footer-height);
  padding: 0.25em;
  background-color: var(--header-bg);
  color: var(--fg-color);
  display: grid;
  place-content: center;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
  font-size: small;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 10;
}

/* Settings Modal Styles */
.settings-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--modal-overlay);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  z-index: 1000;
  overflow-y: auto;
  padding: 16px;
}

.settings-modal {
  background-color: var(--modal-bg);
  border-radius: 8px;
  padding: 16px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  margin: 16px auto;
}

@media (max-width: 768px) {
  .settings-modal {
    width: 95%;
    padding: 12px;
    margin: 8px auto;
  }
}

textarea:focus {
  outline: none;
}

textarea::selection {
  color: var(--header-bg);
  background-color: var(--accent-color);
}


.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.close-button {
  background: none;
  border: none;
  color: var(--fg-color);
  cursor: pointer;
  padding: 4px;
}

.close-button:hover {
  opacity: 0.8;
}

.settings-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.setting-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.setting-group label {
  font-size: 14px;
  color: var(--fg-color);
}

.setting-group select {
  padding: 8px;
  border-radius: 4px;
  border: 1px solid var(--input-border);
  background-color: var(--input-bg);
  color: var(--fg-color);
  font-size: 14px;
}

.install-button {
  padding: 10px;
  background-color: var(--accent-color);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.install-button:hover {
  opacity: 0.9;
}

.settings-button {
  background: none;
  border: none;
  color: var(--accent-color);
  cursor: pointer;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.settings-button:hover {
  opacity: 0.8;
}

.color-presets {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  align-items: center;
}

.color-preset-button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 2px solid var(--input-border);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0;
}

.color-preset-button:hover {
  transform: scale(1.1);
}

.color-picker-wrapper {
  position: relative;
  width: 32px;
  height: 32px;
}

.color-picker {
  position: absolute;
  top: 0;
  left: 0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
}

.color-picker-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--fg-color);
  pointer-events: none;
}

.button-group {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.action-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background-color: var(--input-bg);
  border: 1px solid var(--input-border);
  border-radius: 4px;
  color: var(--fg-color);
  cursor: pointer;
  font-size: 14px;
}

.action-button:hover {
  background-color: var(--accent-color);
  color: white;
}

.reset-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background-color: #ef4444;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  width: 100%;
  justify-content: center;
}

.reset-button:hover {
  opacity: 0.9;
}

.settings-content {
  gap: 16px !important;
}

.button-group {
  gap: 4px !important;
}

.action-button {
  padding: 6px 8px !important;
  font-size: 12px !important;
}


/* Add these to your index.css file */
.footer-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.footer-link {
  display: flex;
  align-items: center;
  gap: 4px;
  color: var(--fg-color);
  text-decoration: none;
  transition: color 0.2s ease;
}

.footer-link:hover {
  color: var(--accent-color);
}

.footer-separator {
  color: var(--fg-color);
  opacity: 0.5;
}

.footer-text {
  display: flex;
  align-items: center;
  gap: 4px;
}

.heart-icon {
  color: var(--accent-color);
}